<template>
	<view class="content">
		<view class="banner">
			<swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000">
				<swiper-item>
					<view class="swiper-item">
						<image :src=img mode="widthFix"></image>
					</view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item">
						<image :src=img mode="widthFix"></image>
					</view>
				</swiper-item>
			</swiper>
		</view>
		<view class="shouye_btn">
			<view class="shouye_btn_content" @click="chooseImage">
				症状拍照
			</view>
			<view class="shouye_btn_content">
				<navigator url="../buliangfanying/buliangfanying">
					记录不良反应
				</navigator>
			</view>
			<view class="shouye_btn_content" @click="chooseImage">
				拍摄症状
			</view>
			<view class="shouye_btn_content">
				<navigator url="../fazuocishu/fazuocishu">
					记录发作次数
				</navigator>
			</view>
		</view>
		<view class="main">
			<!-- 用药提醒 -->
			<view class="yongyao">
				<text>用药提醒</text>
				<view class="main_yongyao">
					<view class="yongyaoContent">
						<view class="date">
							<text>{{week}}</text>
							<text>{{date}}</text>
						</view>
						<view v-for="(item,i) in list" :key="i" class="yongyaoList">
							<text>{{item.yao}}</text>
							<text>{{item.jiliang}}</text>
						</view>
						<view class="yaoBtn">
							<button type="primary" size="mini">今日已服用</button>
						</view>
					</view>
				</view>
			</view>
			<!-- 我的医生 -->
			<view class="myDoc">
				<text>我的医生</text>
				<view class="main_myDoc">
					<view class="myDocContent">
						<view class="head">
							<view class="headImg">
								<!-- 头像 -->
								<image :src=img mode="scaleToFill"></image>
							</view>
							<view class="headText">
								<!-- 名字 -->
								<view>
									<text>{{name}}</text>
									<!-- 职位 -->
									<text>{{zhiwei}}</text>
								</view>
								<view>
									<text>关联时间：{{time}}</text>
								</view>
							</view>
						</view>
						<view class="headTitle">
							<view>
								<text>从医经验：{{jingyan}}</text>
							</view>
							<view>
								<text>所属医院：{{yiyuan}}</text>
							</view>
							<view class="headTitleBar">
								<text>诊疗活动</text>
								<text>患教活动</text>
							</view>
						</view>
						<view class="main-content">
							<!-- 诊疗活动 -->
							<view class="main-content-zhenliao">
								<view v-for="(item,i) in zhenliao" :key="i">
									<view class="zhengliaoLeft">
										<view>{{item.time}}</view>
										<view>{{item.place}}</view>
									</view>
									<view class="zhengliaoRight">
										<view>{{item.zhuangtai}}</view>
									</view>
								</view>
							</view>
							<!-- 患教活动 -->
							<view class="main-content-huanjiao">
								
							</view>
							<view class="more">
								<view>
									<navigator url="../more/more">
										查看更多
									</navigator>
								</view>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				img:"../../static/zhuce/chushi.png",
				week:"第一周",
				date:"2020.1.1~2020.1.7",
				list:[
					{yao:"德巴金缓解片",jiliang:"100mg/日"},
					{yao:"得利多",jiliang:"20mg/日"},
					{yao:"德巴金缓解片",jiliang:"100mg/日"},
					{yao:"得利多",jiliang:"20mg/日"},
					{yao:"德巴金缓解片",jiliang:"100mg/日"},
					{yao:"得利多",jiliang:"20mg/日"}
				],
				name:"倒霉熊",
				zhiwei:"主治医师",
				time:"2020.1.1",
				jingyan:"主治癫痫10年",
				yiyuan:"解放军第一附属医院",
				zhenliao:[
					{time:"2017-12-22 09:12:00 诊疗活动",place:"地点：北京第一癫痫医院",zhuangtai:"报名中"},
					{time:"2017-12-22 09:12:00 诊疗活动",place:"地点：北京第一癫痫医院",zhuangtai:"报名中"},
					{time:"2017-12-22 09:12:00 诊疗活动",place:"地点：北京第一癫痫医院",zhuangtai:"报名中"},
					{time:"2017-12-22 09:12:00 诊疗活动",place:"地点：北京第一癫痫医院",zhuangtai:"报名中"}
				]
			}
		},
		onLoad() {

		},
		methods: {
			onNavigationBarButtonTap(val){
				console.log(val)
				uni.scanCode({
				    success: function (res) {
				        console.log('条码类型：' + res.scanType);
				        console.log('条码内容：' + res.result);
				    }
				});
			},
			chooseImage(){
				uni.chooseImage({
					count: 6, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album','camera'], //从相册选择、摄像头
					success: function(res) {
						console.log(JSON.stringify(res.tempFilePaths));
					}
				});
			}
		}
	}
</script>

<style>
